$containerWidth: 1254; // rng container
$containerGap: 15; // khong cch 2 bn container
$colGap: 30; // khong cch 2 bn ca column
$colMax: 12; // S column ca grid
$breakpoints: (default: xs,
    rotate: 480,
    sm: 768,
    md: 992,
    lg: 1200);

// Reset body
body {
    margin: 0;
    padding: 0;
}

*,
*:before,
*:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

// V d vi container rng 1200px,  y ta thay bng bin $containerWidth
.container {
    max-width: $containerWidth * 1px;
    margin: auto;
}

// Container full chiu rng
.container-fluid {
    width: 100%;
}

// Container padding 2 bn l 15px
.container,
.container-fluid {
    padding-left: $containerGap * 1px;
    padding-right: $containerGap * 1px;
    position: relative;
}

// To row  fix khong cch 2 bn bng vi khong cch 2 bn ca column v fix chiu cao do column float: left to ra bn di
.row {
    margin-left: -($colGap / 2) * 1px;
    margin-right: -($colGap / 2) * 1px;
    display: flex;
    flex-wrap: wrap;

    &:after {
        content: '';
        display: table;
        clear: both;
    }
}

// Column cho khong cch 30px
[class*="col-"] {
    padding-left: ($colGap / 2) * 1px;
    padding-right: ($colGap / 2) * 1px;
    width: 100%;
}

// Vi grid l 12 column th col-xs-3 s l 25%

@for $i from 1 through $colMax {
    .col-xs-#{$i} {
        width: (100 * $i) / $colMax * 1%;
    }
}

@media (min-width: 480px) {
    // col-sm-x >= 480px
}

@media (min-width: 768px) {
    // col-sm-x >= 768px
}

@media (min-width: 992px) {
    // col-md-x >= 992px
}

@media (min-width: 1200px) {
    // col-lg-x >= 1200px
}

@mixin column($nameCol) {
    @for $i from 1 through $colMax {
        .col-#{$nameCol}-#{$i} {
            width: (100 * $i) / $colMax * 1%;
        }
    }
}

@each $key,
$val in $breakpoints {
    @if ($key=='default') {
        // Dng hm map-get ca scss  ly value
        $colDefault: map-get($breakpoints, 'default');
        @include column($colDefault);
    }

    @else {
        @media (min-width: $val * 1px) {
            @include column($key);
        }
    }
}